@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-auto-columns: 100%;
  grid-template-rows: 10rem;
  grid-auto-rows: min-content;
  align-items: center;
}

.icon {
  margin: 0 auto;
  width: 8rem;
  fill: $colorLightest;
}

.label {
  @include fontDescription($colorLightest, 500);

  margin: 3.9rem 0 0 0;
  text-align: center;
  line-height: 2.5rem;
}

.description {
  @include fontRegular;

  margin: 1.6rem 0 0 0;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    grid-template-rows: min-content;
  }

  .icon {
    width: 4rem;
  }

  .label {
    margin: 2rem 0 0 0;
    font-size: 1.8rem;
  }

  .description {
    margin: 1.1rem 0 0 0;
  }
}
