@import "styles/variables";
@import "styles/mixins";

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
}

.wrapper,
.list {
  display: flex;
  align-items: center;
}

.wrapper {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.burger {
  @include resetButton;

  display: none;
}

.nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.list {
  @include resetList;

  margin-left: auto;
}

.item {
  &:not(:first-child) {
    margin-left: 9.7rem;
  }
}

.buttonNavigation {
  @include resetButton;
  @include fontNav;
  @include linkBehavior;
}

.button {
  margin-left: auto;
}

@media (max-width: $adaptiveMax) {
  .burger {
    position: relative;
    display: block;
    margin-left: auto;
    z-index: 3;

    &::before {
      content: "";
      position: absolute;
      top: -1rem;
      left: -1rem;
      width: calc(100% + 2rem);
      height: calc(100% + 2rem);
    }
  }

  .icon {
    width: 1.5rem;
    stroke: white;
    filL: white;
  }

  .nav {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-color: $colorHighlight;
    transform: translateX(100%);
    transition-duration: $transitionDuration;
    z-index: 1;
  }

  .navVisible {
    transform: translateX(0);
  }

  .list {
    flex-direction: column;
    margin-left: 0;
  }

  .item {
    &:not(:first-child) {
      margin-top: 2rem;
      margin-left: 0;
    }
  }

  .button {
    margin-top: 3rem;
    margin-left: 0;
  }
}
