@import "mixins";
@import "fonts";
@import "variables";

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  color: $colorLightest;
  background-color: $colorHighlight;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

a {
  text-decoration: none;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}
