@import "styles/variables";
@import "styles/mixins";

.footer {
  margin-top: auto;
}

.wrapper {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;

  &::before {
    --width: 114rem;
    content: "";
    position: absolute;
    top: 0;
    left: calc(50% - var(--width) / 2);
    width: var(--width);
    height: 1px;
    background-color: $colorLightest;
  }

  .logo {
    margin-left: 37.3rem;
  }

  .copyright {
    @include fontSmall;

    margin-left: auto;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    flex-wrap: wrap;
    padding-top: 0.6rem;
    padding-bottom: 1.4rem;

    &::before {
      --width: 100%;
    }

    .logo {
      margin-left: auto;
    }

    .copyright {
      margin-top: 2rem;
      width: 100%;
    }
  }
}
