@import "styles/mixins";
@import "styles/variables";

.section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.heading {
  @include visuallyHidden;
}

.illustrated {
  margin-top: 18rem;
}

@media (max-width: $adaptiveMax) {
  .illustrated {
    margin-top: 7rem;
  }

  .icon {
    display: block;
    margin: 0 auto;
    width: 15rem;
  }
}
