@import "styles/variables";
@import "styles/mixins";

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.wrapper {
  display: grid;
  grid-template-columns: 56rem 1fr;
  align-items: center;
}

.content {
  grid-column: 1 / 2;
  grid-row: 1 / -1;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 8rem;
}

.heading {
  @include h1;

  margin: 0;
}

.description {
  @include fontDescription;

  margin: 0;

  & + .description {
    margin-top: 0.3rem;
  }
}

.icon {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  width: 45rem;
}

@media (max-width: $adaptiveMax) {
  .section {
    align-items: flex-start;
  }

  .wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 10rem;
  }

  .content {
    padding-top: 0;
  }

  .icon {
    width: 20rem;
  }
}
