@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(2, 28rem);
  align-items: center;
  width: min-content;
  min-height: 6.2rem;
  border: 2px solid $colorLightest;
  border-radius: 3rem;
}

.item {
  position: relative;
  display: flex;
  justify-content: center;
  height: 100%;

  &::before {
    content: "";
    position: absolute;
    top: 0.3rem;
    left: 0.3rem;
    width: calc(100% - 0.6rem);
    height: calc(100% - 0.6rem);
    background-color: $colorLightestMedium;
    border-radius: 2.6rem;
    opacity: 0;
    transition-duration: $transitionDuration;
    pointer-events: none;
  }
}

.itemActive {
  &::before {
    opacity: 1;
  }
}

.tab {
  @include resetButton;
  @include fontDescription($colorLightest, 500);
}

@media (max-width: $adaptiveMax) {
  .list {
    grid-template-columns: 43% 57%;
    width: 100%;
    min-height: 4.4rem;
  }
}
