@import "variables";

// font mixins

@mixin h1 ($color: $colorLightest) {
  font-family: "Heebo", Arial, sans-serif;
  font-size: 4.8rem;
  font-weight: 700;
  line-height: 7rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 2.4rem;
    line-height: 3.5rem;
  }
}

@mixin h2 ($color: $colorLightest) {
  font-family: "Heebo", Arial, sans-serif;
  font-size: 3.6rem;
  font-weight: 700;
  line-height: 5.3rem;
  color: $color;


  @media (max-width: $adaptiveMax) {
    font-size: 2.2rem;
    line-height: 3.2rem;
  }
}

@mixin fontDescription ($color: $colorLightest, $weight: 400) {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 2.2rem;
  font-weight: $weight;
  line-height: 3.6rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.4rem;
    line-height: 2.1rem;
  }
}

@mixin fontRegular ($color: $colorLightest, $weight: 400, $family: "Roboto") {
  font-family: $family, Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: $weight;
  line-height: 2.4rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.4rem;
    line-height: 2.1rem;
  }
}

@mixin fontNav ($color: $colorLightest) {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.9rem;
  color: $color;
}

@mixin fontSmall ($color: $colorLightest) {
  font-family: "Exo2", Arial, sans-serif;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.7rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1rem;
    line-height: 1.2rem;
  }
}

// reset mixins
@mixin resetButton {
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin resetList {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin resetFieldset {
  margin: 0;
  padding: 0;
  border: none;
}

// style mixins
@mixin linkBehavior () {
  transition-duration: $transitionDuration;

  &:hover {
    opacity: 0.7;
  }
}

@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}
