@import "styles/variables";

.section {
  background-color: $colorLightest;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.content {
  align-items: flex-start;
  margin-top: 5.5rem;
  padding-left: 12%;
}

.description {
  margin: 0;

  &:not(:first-child) {
    margin-top: 0.6rem;
  }
}

.icon {
  width: 32.6rem;
}

@media (max-width: $adaptiveMax) {
 .content {
   margin-top: 1.9rem;
   padding-left: 0;
   padding-bottom: 3.5rem;
 }

  .description {
    &:not(:first-child) {
      margin-top: 0;
    }
  }

  .icon {
    width: 28.8rem;
  }
}
