@import "styles/mixins";
@import "styles/variables";

.wrapper {
  display: grid;
  grid-template-columns: 45rem 1fr;
  grid-column-gap: 8.5rem;
  align-items: center;
}

.description {
  @include fontDescription;

  margin: 0.7rem 0 0 0;
}

.wrapperDark {
  .description {
    @include fontRegular($colorDarkest);
  }
}

.wrapperSmaller {
  &:not(.wrapperDark).description {
    @include fontRegular;
  }
}

.wrapperReverse {
  grid-template-columns: 1fr 45rem;

  .description {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  .image {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
  }

  .description {
    margin: 2.3rem 0 0 0;
  }

  .image {
    width: 100%;
  }

  .wrapperReverse {
    display: flex;
    flex-direction: column;

    .description {
      order: 1;
      margin: 0;
    }

    .image {
      order: 2;
      margin-top: 2rem;
    }
  }
}
