@import "styles/mixins";
@import "styles/variables";

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

@media (max-width: $adaptiveMax) {
  .list {
    display: flex;
    flex-direction: column;
  }

  .item {
    &:not(:first-child) {
      margin-top: 5rem;
    }
  }
}
