@import "styles/variables";

.link {
  display: block;
  width: 3.6rem;
  font-size: 0;

  &:hover {
    .icon {
      fill: $colorLightest;
    }

    .iconCircle {
      fill: $colorHighlight;
    }
  }
}

.icon {
  fill: $colorHighlight;
  transition-duration: $transitionDuration;
}

.iconCircle {
  fill: $colorLightest;
  transition-duration: $transitionDuration;
}

@media (max-width: $adaptiveMax) {
  .link {
    width: 2rem;
  }
}
