@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 55.7rem 1fr;
  grid-auto-rows: min-content;
  padding-top: 20%;
  min-height: 100vh;
}

.content {
  position: relative;
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  padding-top: 3rem;

  &::before {
    content: "";
    position: absolute;
    top: -5rem;
    right: calc(-22.4rem - 29.8rem);
    width: 29.8rem;
    height: 37.3rem;
    background-image: url("../../../assets/icons/risks_ill.svg");
    background-repeat: no-repeat;
    object-fit: cover;
  }
}

.description {
  @include fontRegular($colorLightest, 400, "Exo2");

  margin: 0;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
  }

  .content {
    padding-bottom: 32rem;

    &::before {
      top: auto;
      bottom: 7rem;
      left: calc(50% - 15rem / 2);
      width: 15rem;
      height: 18.8rem;
    }
  }
}
