@import "styles/variables";

.section {
  background-color: $colorLightest;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.content {
  margin-top: 5rem;
}

.iconProfit {
  width: 100%;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  .content {
    margin-top: 2.4rem;
  }
}
