@import "styles/variables";

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;

  .list {
    margin-top: 5rem;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 7rem;
    padding-bottom: 3.7rem;

    .list {
      margin-top: 2.2rem;
    }
  }
}
